import classnames from 'classnames';
import {marginType} from "./_margin";
import getNum from "../../src/js/util/_getNum"

const {
    BlockControls,
    InspectorControls,
    PanelColorSettings,
    ContrastChecker,
    getColorClassName,
    withColors,
    InnerBlocks,
    MediaUpload
} = wp.editor;
const {
    PanelBody,
    BaseControl,
    SelectControl,
    RangeControl,
    Button,
    ToggleControl
} = wp.components;
const {Fragment} = wp.element;
const {__} = wp.i18n;

export default function (props) {
    const {
        attributes,
        setAttributes,
        backgroundColor,
        textColor,
        setBackgroundColor,
        setTextColor,
    } = props;
    const {
        wrapperTag,
        marginTop,
        marginBottom,
        paddingTop,
        paddingBottom,
        paddingLeft,
        paddingRight,
        backgroundImageURL,
        backgroundImageAlt,
        backgroundImageID,
        backgroundImageOpacity,
        backgroundSkew,
        backgroundSkewWidth,
        innerCustomWidth
    } = attributes;

    const rangeStep = 0.5;

    /**
     * HTML
     */
    const wrapperTagNames = [
        {
            tag: 'div'
        },
        {
            tag: 'section'
        },
        {
            tag: 'aside'
        }
    ];
    const Wrapper = wrapperTag;
    /**
     * 
     */
    const ALLOWED_MEDIA_TYPES = ['image'];
    const hasBackgroundImage = backgroundImageURL && !backgroundSkew;
    const hasBackgroundDim = backgroundImageURL && !backgroundSkew;
    const backgroundDimClass = 'has-background-dim-' + (10 * Math.round(backgroundImageOpacity / 10));

    /**
     * 
     */
    const classes = classnames(
        'ystdb-section',
        {
            'has-background': backgroundColor.color,
            [backgroundColor.class]: backgroundColor.class,
            'has-text-color': textColor.color,
            [textColor.class]: textColor.class,
            'has-background-image': hasBackgroundImage,
            'has-background-dim': hasBackgroundDim,
            [backgroundDimClass]: hasBackgroundDim
        }
    );
    /**
     * 
     */
    const innerClasses = 'ystdb-section__inner';
    /**
     * 
     */
    const editWrapStyle = {
        paddingTop: 0 === marginTop ? 0 : marginTop + 'rem',
        paddingBottom: 0 === marginBottom ? 0 : marginBottom + 'rem',
    };
    /**
     * 
     */
    const sectionStyles = {
        backgroundColor: (backgroundColor.color && !backgroundSkew) ? backgroundColor.color : undefined,
        color: textColor.color,
        paddingTop: 0 === paddingTop ? 0 : paddingTop + 'rem',
        paddingBottom: 0 === paddingBottom ? 0 : paddingBottom + 'rem',
        backgroundImage: hasBackgroundImage ? `url("${backgroundImageURL}")` : undefined,
        paddingLeft: '1rem',
        paddingRight: '1rem'
    };
    /**
     * 
     */
    const innerStyles = {
        maxWidth: innerCustomWidth !== 0 ? innerCustomWidth : undefined,
        marginRight: 'auto',
        marginLeft: 'auto',
        paddingLeft: 0 === paddingLeft ? 0 : paddingLeft + 'rem',
        paddingRight: 0 === paddingRight ? 0 : paddingRight + 'rem'
    };
    /**
     * 
     */
    const mediaUploadRender = (obj) => {
        if (0 === backgroundImageID) {
            return (
                <Button
                    isDefault
                    onClick={obj.open}
                >
                    {__('', 'ystandard-blocks')}
                </Button>
            );
        } else {
            return (
                <div>
                    <Button
                        onClick={obj.open}
                        className={'ystdb-mediaupload__preview'}
                        style={{padding: 0}}
                    >
                        <img src={backgroundImageURL} alt={backgroundImageAlt}/>
                    </Button>
                    <Button
                        isDefault
                        onClick={() => {
                            setAttributes({
                                backgroundImageURL: '',
                                backgroundImageID: 0
                            });
                        }}
                    >
                        {__('', 'ystandard-blocks')}
                    </Button>
                </div>
            );
        }
    };
    /**
     * 
     */
    const skewMask = () => {
        if (backgroundSkew) {
            const backgroundSkewValue = backgroundSkew + 'deg';
            const skewStyle = {
                height: backgroundSkewWidth ? backgroundSkewWidth + '%' : undefined,
                backgroundColor: backgroundColor.color,
                transform: `skewY(${backgroundSkewValue}) translateY(-50%)`
            };
            const skewClass = classnames(
                'ystdb-section__mask'
            );
            return (
                <div className={skewClass} style={skewStyle}></div>
            );
        }
    };

    return (
        <Fragment>
            <InspectorControls>
                <div className="ystdb-inspectorcontrols">
                    <PanelBody title={__('', 'ystandard-blocks')}>
                        <BaseControl label={__('()', 'ystandard-blocks')}>
                            <div className={`ystdb-info__label`}></div>
                            <div className={'ystdb-btn-selector components-base-control'}>
                                {marginType.margin.map((item) => {
                                    return (
                                        <Button
                                            isDefault
                                            onClick={() => {
                                                setAttributes({
                                                    marginTop: item.num,
                                                    marginBottom: item.num,
                                                });
                                            }}
                                        >
                                            <span>{item.label}</span>
                                        </Button>
                                    );
                                })}
                            </div>
                            <RangeControl
                                label={__('', 'ystandard-blocks')}
                                value={marginTop}
                                onChange={(value) => setAttributes({marginTop: getNum(value, -10, 10, 0)})}
                                min={-10}
                                max={10}
                                step={rangeStep}
                            />
                            <RangeControl
                                label={__('', 'ystandard-blocks')}
                                value={marginBottom}
                                onChange={(value) => setAttributes({marginBottom: getNum(value, -10, 10, 0)})}
                                min={-10}
                                max={10}
                                step={rangeStep}
                            />
                            <p>
                                <span className={`ystdb-info__small`}></span>
                                <span className={`ystdb-info__small`}>rem</span>
                            </p>
                        </BaseControl>
                        <BaseControl label={__('()', 'ystandard-blocks')}>
                            <div className={`ystdb-info__label`}></div>
                            <div className={'ystdb-btn-selector components-base-control'}>
                                {marginType.padding.map((item) => {
                                    return (
                                        <Button
                                            isDefault
                                            onClick={() => {
                                                setAttributes({
                                                    paddingTop: item.num,
                                                    paddingBottom: item.num,
                                                });
                                            }}
                                        >
                                            <span>{item.label}</span>
                                        </Button>
                                    );
                                })}
                                <br/>
                                <div>
                                    <span className={`ystdb-info__small`}></span>
                                </div>
                            </div>
                            <RangeControl
                                label={__('', 'ystandard-blocks')}
                                value={paddingTop}
                                onChange={(value) => setAttributes({paddingTop: getNum(value, 0, 10)})}
                                min={0}
                                max={10}
                                step={rangeStep}
                            />
                            <RangeControl
                                label={__('', 'ystandard-blocks')}
                                value={paddingBottom}
                                onChange={(value) => setAttributes({paddingBottom: getNum(value, 0, 10)})}
                                min={0}
                                max={10}
                                step={rangeStep}
                            />
                            <RangeControl
                                label={__('', 'ystandard-blocks')}
                                value={paddingLeft}
                                onChange={(value) => setAttributes({paddingLeft: getNum(value, 0, 5)})}
                                min={0}
                                max={5}
                                step={rangeStep}
                            />
                            <RangeControl
                                label={__('', 'ystandard-blocks')}
                                value={paddingRight}
                                onChange={(value) => setAttributes({paddingRight: getNum(value, 0, 5)})}
                                min={0}
                                max={5}
                                step={rangeStep}
                            />
                            <div>
                                <span className={`ystdb-info__small`}></span>
                                <span className={`ystdb-info__small`}>rem</span>
                            </div>
                        </BaseControl>
                    </PanelBody>
                    <PanelBody title={__('', 'ystandard-blocks')}>
                        <div className={`ystdb-info__label`}></div>
                        <div className={'ystdb-btn-selector components-base-control'}>
                            {marginType.innerWidth.map((item) => {
                                return (
                                    <Button
                                        isDefault
                                        onClick={() => {
                                            setAttributes({
                                                innerCustomWidth: item.num
                                            });
                                        }}
                                    >
                                        <span>{item.label}</span>
                                    </Button>
                                );
                            })}
                        </div>
                        <RangeControl
                            label={__('', 'ystandard-blocks')}
                            value={innerCustomWidth}
                            onChange={(value) => setAttributes({innerCustomWidth: getNum(value, 0, 1920, 960)})}
                            min={0}
                            max={1920}
                            step={16}
                            allowReset={true}
                        />
                        <p>
                            <span className={`ystdb-info__small`}>0</span>
                        </p>
                    </PanelBody>
                    <PanelBody
                        title={__('HTML', 'ystandard-blocks')}
                        initialOpen={false}
                    >
                        <p>
                            <span className={`ystdb-info__small`}>HTML</span>
                        </p>
                        <div className={'ystdb-btn-selector components-base-control'}>
                            {wrapperTagNames.map((item) => {
                                return (
                                    <Button
                                        isDefault
                                        isPrimary={wrapperTag === item.tag}
                                        onClick={() => {
                                            setAttributes({wrapperTag: item.tag});
                                        }}
                                    >
                                        <span>{item.tag}</span>
                                    </Button>
                                );
                            })}
                        </div>
                    </PanelBody>
                    <PanelBody
                        title={__('', 'ystandard-blocks')}
                        initialOpen={false}
                    >
                        <MediaUpload
                            onSelect={(media) => {
                                setAttributes({
                                    backgroundImageURL: media.url,
                                    backgroundImageID: media.id,
                                    backgroundImageAlt: media.alt
                                });
                            }}
                            type={ALLOWED_MEDIA_TYPES}
                            value={backgroundImageID}
                            render={mediaUploadRender}
                        />
                        <br/><br/>
                        <RangeControl
                            label={__('', 'ystandard-blocks')}
                            value={backgroundImageOpacity}
                            onChange={(value) => setAttributes({backgroundImageOpacity: getNum(value, 0, 100)})}
                            min={0}
                            max={100}
                            step={10}
                        />
                        <p>
                            <span className={`ystdb-info__small`}></span>
                            <span className={`ystdb-info__small`}></span>
                        </p>
                    </PanelBody>
                    <PanelBody
                        title={__('', 'ystandard-blocks')}
                        initialOpen={false}
                    >
                        <p>
                            <span className={`ystdb-info__small ystdb-info__bold`}></span>
                        </p>
                        <RangeControl
                            label={__('', 'ystandard-blocks')}
                            value={backgroundSkew}
                            onChange={(value) => setAttributes({backgroundSkew: getNum(value, -3, 3, 0)})}
                            min={-3}
                            max={3}
                            step={0.5}
                            allowReset={true}
                        />
                        <RangeControl
                            label={__('', 'ystandard-blocks')}
                            value={backgroundSkewWidth}
                            onChange={(value) => setAttributes({backgroundSkewWidth: getNum(value, 70, 100, 90)})}
                            min={70}
                            max={100}
                            step={1}
                            allowReset={true}
                        />
                        <p>
                                <span
                                    className={`ystdb-info__small`}>()</span>
                            <span className={`ystdb-info__small`}></span>
                        </p>
                    </PanelBody>
                    <PanelColorSettings
                        title={__('Color Settings')}
                        initialOpen={true}
                        colorSettings={[
                            {
                                value: backgroundColor.color,
                                onChange: (color) => {
                                    setBackgroundColor(color);
                                },
                                label: __('Background Color'),
                            },
                            {
                                value: textColor.color,
                                onChange: (color) => {
                                    setTextColor(color);
                                },
                                label: __('Text Color'),
                            },
                        ]}
                    >
                        <ContrastChecker
                            backgroundColor={backgroundColor.color}
                            textColor={textColor.color}
                        />
                    </PanelColorSettings>

                </div>
            </InspectorControls>

            <div className={'ystdb-section__edit-wrap'} style={editWrapStyle}>
                <div className={classes} style={sectionStyles}>
                    {skewMask()}
                    <Wrapper className={innerClasses} style={innerStyles}>
                        <InnerBlocks/>
                    </Wrapper>
                </div>
            </div>
        </Fragment>
    );
}